import React, { PureComponent } from "react";
import Row from "./elements/Row";
import { getPostWidthGalleryTheme_slider, IS_SCREEN_SHOT, checkPreview , findFromArray, removeTransFromCss, IMG_LOAD_AFTER_COMPLETE_LAYOUT} from '../../../utils'
import { getDataNextSteps, themePostTracking, showPopUP } from '../../../actions/themeActions'
import { connect } from 'react-redux';

import "@splidejs/splide/dist/css/themes/splide-default.min.css";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";

class GalleryTheme extends PureComponent {
  allImageQueue = [];
  state = {
    cardWidth: null,
    largeDiv: null,
    smallDiv: null,
    allFirstImage: false
  }


  requestData = () => {
    const { wall, postData, appendData, hasMoreData, loaderData } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    const timeStamp = Math.floor(Date.now() / 1000);
    if (hasMoreData && !loaderData.isShowMoreLoading) this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent);
  }
  

  componentDidMount() {
    this.onCollageDataFormation(this.props)
    const { wall, postData, appendData } = this.props;

    let fixedHeightScrollBar = document.getElementById("scrlBarComanW")


    if (appendData && wall.Personalization.autoScrollStatus) {
      window.addEventListener('scroll', (event) => {
        let fixedHeightScrollBar = document.getElementById("scrlBarComanW")
        let documentHeight = document.body.scrollHeight;
        var getBottom = window.innerHeight + window.scrollY;

        if (fixedHeightScrollBar.scrollHeight - 10 < getBottom) {
          setTimeout(() => this.requestData(), 1000)
        }

      });
    }
  }


  onCollageDataFormation = (props) => {
    const { wall, postData } = props;
    var widnowWidth = document.querySelector('.taggshopContainer') ? document.querySelector('.taggshopContainer').clientWidth : '';
    const ColumnCount = wall.ThemeRule.numberOfCoumn;
    const { cardWidth, largeDiv, smallDiv } = getPostWidthGalleryTheme_slider(widnowWidth, ColumnCount, wall.Personalization.commerceTheme);
    this.setState({ cardWidth: cardWidth, largeDiv: largeDiv, smallDiv: smallDiv })

  }


  onUpdateData = () => {
    setTimeout(() => this.requestData(), 1000)
  }

  // requestData = () => {
  //   const { wall, postData, appendData, hasMoreData, loaderData } = this.props;
  //   const postCount = wall.ThemeRule.numberOfPosts;
  //   const timeStamp = Math.floor(Date.now() / 1000);
  //   if (hasMoreData && !loaderData.isShowMoreLoading) this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent);
  // }


  handleAllImageError = (e) => {

    try {
      const src = e.target.getAttribute("data-item-id");
  
      // if (!this.allImageQueue.some(i => i.src.includes(src))) {
        this.allImageQueue.push({ src, e });
        if (!this.state.allFirstImage) this.processAllImageQueue();
        // if (!this.state.allFirstImage) IMG_LOAD_AFTER_COMPLETE_LAYOUT(e, () => {});
      // }
    } catch (error) {
    }

  };

  processAllImageQueue = async () => {
    if (this.allImageQueue.length) {
      this.setState({ allFirstImage: true })
      const { src, e } = this.allImageQueue[0];
      if (src) {
        IMG_LOAD_AFTER_COMPLETE_LAYOUT(e, this.nextProcessAllImageQueue)
      }
    }
  };
  nextProcessAllImageQueue = () => {
    try {
      this.allImageQueue.shift();
      this.processAllImageQueue()
    } catch (error) {
    }
  }


  render() {
    const { data, cardWidth, largeDiv, smallDiv } = this.state;
    const { postData, completeDataObject, adjustWidth, wall, webFilters, languageSetting, newPostData, loaderData, hasMoreData } = this.props;
    const isOnSiteUpload = wall.UserRule.on_site_upload && (!window.isEditor || checkPreview());
    const sliderSettings1 = {
      type:'slide', // 'loop' or 'slide'
      autoplay: wall.ThemeRule.slidePost == 1 ? true : false,
      interval: wall.ThemeRule.slidePost == 1 ? (parseInt(wall.ThemeRule.slideDuration) * 1000) : 5000,
      speed:1300,
      perPage: 2.5,
      perMove: 2,
      focus: 0, 
      waitForTransition : false,
      updateOnMove : true, 
      breakpoints: {
        480: {
          perPage: 1.5,
          perMove: 1,
        },
      },
    }


    return (
      <div className="tb_gst_post_container" id="ts_theme_container">
        <Splide hasTrack={false} options={sliderSettings1}>

          <SplideTrack>
            {
              (newPostData && newPostData.length > 0) ? newPostData.map((dataArr, index) => {

                // if (IS_SCREEN_SHOT && newPostData && ((newPostData.length - 1) == index)) {
                //   setTimeout(() => {
                //     let dataUpdateImage = document.querySelectorAll("img[data-is-optmized='0']");
                //     if (dataUpdateImage && dataUpdateImage.length > 0) {
                //       dataUpdateImage.forEach((item) => {
                //         item.setAttribute("src", item.getAttribute("data-src"))
                //         item.setAttribute("data-is-optmized", "1")
                //       })
                //     }
                //   }, 1400)
                // }
                const isIndex1Onsite = index == 0 && isOnSiteUpload && dataArr.length == 2;
                return dataArr.length == 3 || isIndex1Onsite ? <SplideSlide key={`splide_Id${index}`}><Row dataArr={dataArr} handleAllImageError={this.handleAllImageError} key={`post_id_${index}`} onSiteUploadShow={isIndex1Onsite} indexNo={index < 6 ? index : '0'} {...this.props} cardWidth={cardWidth} largeDiv={66.66} smallDiv={33.33} /></SplideSlide> : ''
              }) : null
            }

          </SplideTrack>

          <div className="splide__arrows">
            <div className=" splide__arrow--next tb_gs_slider_next_arrow-next" onClick={this.onUpdateData}>
              {(hasMoreData && loaderData.isShowMoreLoading) ?
                <svg width='26px' height='26px' xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid" className="uil-ring-alt"><rect x="0" y="0" width="100" height="100" fill="none" className="bk"></rect><circle cx="50" cy="50" r="40" stroke="#afafb7" fill="none" strokeWidth="10" strokeLinecap="round"></circle><circle cx="50" cy="50" r="40" stroke="#000000" fill="none" strokeWidth="6" strokeLinecap="round"><animate attributeName="stroke-dashoffset" dur="2s" repeatCount="indefinite" from="0" to="502"></animate><animate attributeName="stroke-dasharray" dur="2s" repeatCount="indefinite" values="150.6 100.4;1 250;150.6 100.4"></animate></circle></svg>
                :
                <div className="splideArrow_inner">
                  <div className="tb__icon tb-chevron-right"><div></div></div>
                </div>
              }
            </div>

            <div className=" splide__arrow--prev tb_gs_slider_pre_arrow-prev">
              <div className="splideArrow_inner">
                <div className="tb__icon tb-chevron-left"><div></div></div>
              </div>
            </div>


          </div>


        </Splide>

      </div>
    );
  }
}

const mapStateToProps = state => {
  const { appData } = state;
  return {
    appData: appData,
    loaderData: state.loaderData,
    newPostData: appData.newPostData,
    appendData: appData.postData.appendData,
    hasMoreData: appData.postData.hasMoreData[0] ? appData.postData.hasMoreData[0].hasMoreData == false ? false : appData.postData.hasMoreData[appData.postData.appendData.networkID] ? appData.postData.hasMoreData[appData.postData.appendData.networkID].hasMoreData : true : true
  }
}
const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),
    showPopUP: (data) => dispatch(showPopUP(data)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(GalleryTheme);